.projects-page-container {
  ul.taxonomy-pills {
    padding-left: 1px;
  }

  ul.taxonomy-pills li {
    display: inline-block;
    padding: 1.5rem 2rem 1.5rem 0rem;
  }
  ul.taxonomy-pills li:hover {
    text-decoration: underline;
  }
  .taxonomy-term-link:focus {
    outline: none;
  }

  .taxonomy-pills .taxonomy-term-link {
    text-decoration: none;
  }
  .taxonomy-term-link.active {
    font-weight: bold;
    text-decoration: underline;
    color: black;
  }
  .projects-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr); /* Default to 3 columns */
    grid-column-gap: 1rem;
    grid-row-gap: 1rem;
    transition: opacity 0.5s ease-in-out;
    opacity: 1;
  }

  /* Responsive Design - Tablets and small desktops */
  @media (max-width: 1024px) {
    .projects-container {
      grid-template-columns: repeat(2, 1fr); /* 2 columns for tablets and smaller screens */
    }
  }

  /* Responsive Design - Mobile devices */
  @media (max-width: 768px) {
    .projects-container {
      grid-template-columns: 1fr; /* 1 column for mobile devices */
    }
  }

  .projects-container.fade-out {
    opacity: 0;
  }

  .projects-container.fade-in {
    opacity: 1;
  }

  .project-tile-container {
    position: relative;
    display: block; /* Ensure proper sizing within the grid */
    overflow: hidden;
    padding-bottom: 75%; /* Maintain 4:3 aspect ratio */
    height: 0; /* Remove fixed height */
  }

  .project-tile-container .overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background: rgba(0, 0, 0, 0.5); /* Black background with 50% opacity */
    color: white;
    padding: 10px;
    box-sizing: border-box;
  }

  .project-tile-container .overlay h2 {
    margin: 0;
    font-size: 18px;
    line-height: 1.4;
  }

  /* Adjust the overlay padding and font sizes for smaller screens */
  @media (max-width: 768px) {
    .project-tile-container .overlay {
      padding: 5px;
    }

    .project-tile-container .overlay h2 {
      font-size: 16px; /* Adjust font size for mobile devices */
    }
  }

  .project-tile-container img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover; /* Ensure images cover the container properly */
  }
}
